.Board {
  background: black;
}

.Board .row {
  display: flex;
}

.Board .cell {
  --side: calc((100vmin - 2px)/24);
  --border: calc(var(--side) / 10);
  --hue: calc(19 * var(--idx) * 12deg);

  width: var(--side);
  height: var(--side);

  border-left: var(--border) solid hsl(var(--hue), 100%, 70%);
  border-top: var(--border) solid hsl(var(--hue), 100%, 70%);
  border-bottom: var(--border) solid hsl(var(--hue), 100%, 30%);
  border-right: var(--border) solid hsl(var(--hue), 100%, 30%);
  background: hsl(var(--hue), 100%, 50%);
}

.Board .cell.empty {  }
.Board .cell.color-0  { --idx: 0; }
